.App {
  text-align: center;
  height: 100%;
  display:flex;
  flex-direction: column;
  background-color: #0c0c25;
  width:100%;
}

button {
  border: none
}

.App-header {
  background-color: #0e7448;
  display: flex;
  flex-direction: row;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  padding: 1.5rem 0;
  flex:1.5;
  -webkit-box-shadow:0px 1px 20px #a504b4;
  -moz-box-shadow:0px 1px 20px #a504b4;
  box-shadow:0px 1px 20px #a504b4;
}

.bottom-header {
  background-color: #04b468;
  display: flex;
  flex-direction: row;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  /* border: solid 1px red; */
  padding: 0.5rem 0;
  flex:1;
  max-width: 90%;
  max-height: 80px;
  margin-top: auto;
  border-radius: 1.5rem 1.5rem 0 0;
}

.title {
  margin: 0;
  padding: 0;
  font-weight: 100;
  /* border: solid 1px red; */
  margin-right: auto;
  margin-left: 1rem;
  font-size: 2rem;
  text-align: left;
}

.sub-title {
  margin: 0;
  padding: 0;
  font-weight: 100;
  /* border: solid 1px red; */
  margin-right: auto;
  margin-left: 1rem;
  font-size: 1rem;
  text-align: left;
}

.typed-title {
  margin: 0;
  padding: 0;
  font-weight: 100;
  margin-left: 1rem;
  font-size: 2.5rem;
  color: #0e8581
}

p {
  z-index: 100;
}

.typed-sub-title {
  margin-left: 1rem;
  font-size: 1.75rem;
  color: #4D77FF;
  font-weight: 600;
}

.section-title {
  font-size: 2.75rem;
  color: white;
}

.bg1 {
  background-color: #0e8581;
}

.bg2 {
  background-color: #30c785;
}

.section-bg {
  text-align: left;
  padding: 0.5rem 1rem;
  border-radius: 1rem;
  margin-left: 1rem;
  margin-top: 2.5%;
  display: inline-block;
  white-space: nowrap;
  margin-right: auto;
}

.white {
  color: white;
}

 .white .section-title {
  color: black
}

.section-text {
  text-indent: 20px;
  font-size: 1.75rem;
  color: white;
  text-align: left;
  margin: 2.5rem 1.5rem
}

.black {
  color: black
}

.bg-white {
  background-color: white;
}

.header-icon-wrapper {
  width:25px;
  height:25px;
  margin-left: auto;
  margin-right: 1rem;
  margin-bottom: 1rem;
  transition: 0.25s;
}

.header-icon {
  width: 25px;
  height:25px;
  padding: 7.5px;
  transition: 0.2s;
  z-index: 1000000;
  
}

.header-icon:hover {
  width:32.5px;
  height:32.5px;
  padding:0px;
  transition: 0.2s;
}

.medium-img {
  width: 40%;
  margin: 1rem 0.5rem;
  z-index: 100000;
}

.about-me-images {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-around;
}

a {
  text-decoration: none;
}

button{
  cursor: pointer;
  display: inline-block;
  position: relative;
  transition: 0.5s;
}

a text {
  color: white;
  align-self: center;
}

.resume-button {
  background-color: #0e8581;
  display: inline-block;
  white-space: nowrap;
  margin: 0 auto;
  padding: 0.5rem 1rem;
  border-radius: .25rem;
  transition: 0.5s;
  display: inline-block;
  position: relative;
}

.icon-container {
  flex-direction: row;
  margin-left: auto;
}

.resume-button:after {
  color:white;
  content: 'Â»';
  position: absolute;
  opacity: 0;  
  right: -20px;
  transition: 0.5s;
  font-size: 1rem;
}

.resume-button:hover{
  padding-right: 24px;
  padding-left:8px;
}

.resume-button:hover:after {
  opacity: 1;
  right: 10px;
}

@media (max-width: 1200px) {
  .title {
    margin-left: .75rem;
    font-size: 1.5rem;
  }

  .typed-title {
    margin-left: .75rem;
    font-size: 1.75rem;
  }

  .typed-sub-title {
    margin-left: .75rem;
    font-size: 1.5rem;
  }

  .sub-title {
    margin-left: 0.75rem;
    font-size: 0.75rem;
  }

  .section-title{
    margin-right: auto;
    font-size: 2.25rem;
  }
}

.header-left {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}

.App-link {
  color: #61dafb;
}

.sorted-text {
  font-size: 1.25rem;
  font-weight: 200;
  color: white;
  max-height: 40px;
  min-height: 40px;
}

.body {
  display: flex;
  flex-direction: column;
  padding: 1rem 0;
}

.main-wrapper {
  display: flex;
  flex-direction: row;
  align-items: flex-end;
  justify-content: center;
  min-height: 80.5%;

  min-width: 95%;
  height: auto;
  margin: 0rem 2rem;
  border-radius: 0.75rem;
  background-color: #020b49;
}

.sorted-line {
  background-color: white;
  width: 5px;
  margin: 0 1px;
}

.settings-wrapper {
  width: 80%;
  margin-right: 1rem;
  margin-left: 1rem;
  background-color: white;
  /* background-color: #222831; */
  border-radius: 0.75rem;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  padding: 1rem 0
}

.box-area {
	top: 0;
	left: 0;
	height: 100%;
  width: 85vw;
  overflow: hidden;
  margin: 0 auto;
}

.ts-sub-title {
  font-size: 1.75rem;
  color: #5463FF;
  list-style-type: none;
  font-weight: 600;
}

.ts-img {
  height: 64px;
  width: 64px;
  margin-top: 0.25rem;
}


.sl {
  width: 128px
}

.pt {
  height: 74px;
}

.ts-bullets {
  font-size: 1.5rem;
  color: #65C18C;
  list-style-type: none;
  font-weight: 600;
}

li {
  list-style: none;
  margin: 0.5rem 0;
}

.ts-wrapper {
  text-align: left;
  display: flex;
  flex-direction: column;
}

.ts-content {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
}

.box-area li {
	position: absolute;
	display: block;
	list-style: none;
	width: 25px;
	height: 25px;
	background: rgba(255, 255, 255, 0.2);
	animation: animate 20s linear infinite;
	bottom: -25%;
}
.box-area li:nth-child(1) {
	left: 86%;
	width: 80px;
	height: 80px;
	animation-delay: 0s;
  background: #0e8581;
  opacity: 0.1;
  z-index: 0;
}

.box-area li:nth-child(2) {
	left: 12%;
	width: 30px;
	height: 30px;
	animation-delay: 1.5s;
	animation-duration: 6s;
}
.box-area li:nth-child(3) {
	left: 70%;
	width: 100px;
	height: 100px;
	animation-delay: 5.5s;
}
.box-area li:nth-child(4) {
	left: 42%;
	width: 150px;
	height: 150px;
	animation-delay: 0s;
	animation-duration: 15s;
}

.box-area li:nth-child(5) {
	left: 33%;
	width: 30px;
	height: 30px;
	animation-delay: 12s;
	animation-duration: 7s;
  background-color: #30c785;
  bottom:-5%;
}

@keyframes animate {
	0% {
		transform: translateY(0) rotate(0deg);
		opacity: 0.5;
	}
	100% {
		transform: translateY(-750px) rotate(360deg);
		opacity: 0;
	}
}
